import wx from 'weixin-js-sdk'
import {getApi} from '../../api/getApi.js'

export function wxInit (sd) {
  let links = `${window.location.href}`
  let title = sd.title
  let desc = sd.summary
  let imgUrl = sd.thumb
  wx.ready(function () {
    wx.onMenuShareTimeline({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    // 
    wx.onMenuShareAppMessage({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareQQ({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareWeibo({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareQZone({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
  })
  wx.error(function (res) {
    // configerrorconfigdebugresSPA
  })
}

export function share (val) {
  let url = encodeURIComponent(`${window.location.href}`)
  getApi(`/sign?share_url=${url}`).then(res => {
    if (res.data.errorCode === 0) {
      wx.config({
        debug: false,
        appId: res.data.data.signPackage.appId,
        timestamp: res.data.data.signPackage.timestamp,
        nonceStr: res.data.data.signPackage.nonceStr,
        signature: res.data.data.signPackage.signature,
        jsApiList: [
          'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareWeibo', 'onMenuShareQZone'
        ]
      })
      wxInit(val)
    }
  })
}



